@use "layout";

$sudoku-dark: #675e73;
$sudoku-light: #8b7c99;

$header-height: 6rem;
$footer-height: 1.5rem;

$content-margin: 3rem;

* {
	outline: 1px dotted #000;
}

html {
    font-family: 'Mulish', 'Muli', sans-serif;
    font-weight: 500;
}

body {
    display: flex;
    flex-direction: column;

    height: 100vh;
}


// Flexbox shared between header and main.
.content {
    max-width: 180vh;
    margin: 0 auto;
    display: flex;
    flex: 1;
}
.content-row {
    display: flex;
    flex: 1;

    margin: 0 $content-margin;

    display: flex;
    align-items: stretch;
    flex-direction: row;
    justify-content: space-between;

    > * {
        margin: 0 3rem 0 0;
    }
    > :last-child {
        margin: 0;
    }
}

// Individual element layout.
header {
    flex: 0 0 auto;
    height: $header-height;

    background-color: #f88;


    .header-left {
        flex: 1 1 100%;
        align-self: baseline;
    }
    .header-center {
        flex: 0 1 auto;
        align-self: baseline;
        text-align: center;

        h1 {
            font-size: 2rem;
            white-space: nowrap;
            margin-bottom: 0.5rem;
        }

        .setter {
            white-space: nowrap;
            /* Maybe this needs a different font size. Not sure. */
        }
    }
    .header-right {
        flex: 1 1 100%;
        align-self: baseline;

        text-align: right;
    }
}


main {
    flex: 1 0 auto;

    display: flex;
    > .content {
        flex: 1;
    }

    .left-panel {
        flex: 1 1 100%;

        > ul >li::before {
            content: "â–¾ ";
            /* This can't be the right way to do this, can it? The intent is to have a drop-down tree menu. */
        }

        * {
            padding: 0;
            list-style-type: none;
        }

        form > div {
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }

        background-color: #8f8
    }

    .sudoku {
        flex: 0 0 auto;

        /* maintain square aspect ratio */
        $size: min(calc(70vw - 20em), calc(100vh - #{$header-height + $footer-height}));
        width: $size; height: $size;

        color: white;
        background-color: $sudoku-dark;
    }

    .right-panel {
        flex: 1 1 100%;
        background-color: #88f;
    }

    @media (max-width: 1200px) and (max-aspect-ratio: 5/4) {
        .content-row {
            flex-direction: column;
        }
        .sudoku {
            margin: 0;
            $size: min(calc(100vw - #{2 * $content-margin}), 50vh);
            width: $size; height: $size;

            align-self: center;
        }
        .left-panel {
            display: none;
        }
    }
}

footer {
    flex: 0 0 auto;
    height: $footer-height;
    line-height: $footer-height;

    font-size: 0.8rem;
    text-align: center;
}
